@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:200);
body {
  margin: 0;
  padding: 0;
  font-family: "Source Code Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 200;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes show {
  0% {
    opacity: 0;
    -webkit-filter: blur(5px);
            filter: blur(5px); }
  100% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0); } }

@keyframes show {
  0% {
    opacity: 0;
    -webkit-filter: blur(5px);
            filter: blur(5px); }
  100% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0); } }

@-webkit-keyframes hide {
  0% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0); }
  100% {
    opacity: 0;
    -webkit-filter: blur(5px);
            filter: blur(5px); } }

@keyframes hide {
  0% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0); }
  100% {
    opacity: 0;
    -webkit-filter: blur(5px);
            filter: blur(5px); } }

main {
  height: 100vh;
  width: 100vw;
  background: #efefef;
  font-size: 27px; }
  main .content {
    width: 500px;
    margin: 0 auto;
    padding: 10% 0;
    color: #b4b4b4;
    box-sizing: border-box;
    word-break: break-all; }
    main .content span {
      opacity: 0;
      -webkit-filter: blur(5px);
              filter: blur(5px);
      transition: opacity 1s;
      position: absolute;
      transition: -webkit-transform .5s ease;
      transition: transform .5s ease;
      transition: transform .5s ease, -webkit-transform .5s ease;
      -webkit-animation: show .5s ease 0s forwards;
              animation: show .5s ease 0s forwards; }
      main .content span.hidden {
        -webkit-filter: blur(0);
                filter: blur(0);
        opacity: 1;
        -webkit-animation: hide .5s ease 0s forwards;
                animation: hide .5s ease 0s forwards; }

